import CloseIcon from '@mui/icons-material/Close';
import Box from '@mui/material/Box';
import Dialog, { DialogProps } from '@mui/material/Dialog';
import IconButton from '@mui/material/IconButton';
import { ModalWrapperProps } from '@libs/dynamic-modal';

export type WrapperProps = Partial<DialogProps> & {
  layout?: 'none' | 'default';
};
export default function Wrapper({
  Component,
  props,
  wrapperProps,
}: ModalWrapperProps<WrapperProps>) {
  return (
    <Dialog
      onClose={props?.modalThis?.close}
      fullWidth
      {...wrapperProps}
      open={true}
    >
      {wrapperProps.layout === undefined ||
      wrapperProps.layout === 'default' ? (
        <Box display="flex" flexDirection="column">
          <Box
            bgcolor="primary.main"
            py={1}
            px={2}
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            color="white"
            fontSize="1.2em"
            fontWeight="bold"
          >
            {wrapperProps.title || <div></div>}
            <IconButton
              size="small"
              sx={{ color: 'white' }}
              onClick={() => props.modalThis?.close()}
            >
              <CloseIcon />
            </IconButton>
          </Box>
          <Component {...props} />
        </Box>
      ) : (
        <Component {...props} />
      )}
    </Dialog>
  );
}
